<template>
  <div>
		<!-- 面包屑导航区 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>

		<!-- 卡片视图 -->
		<el-card>
			<!-- 添加角色 -->
			<el-row>
				<el-col>
					<el-button type="primary">添加角色</el-button>
				</el-col>
			</el-row>

			<!-- 角色列表区 -->
			<el-table :data="rolelist" border stripe row-key="id">
				<!-- 展开列 -->
				<el-table-column type="expand">
					<template slot-scope="scope">
						<el-row :class="['bdbottom', i1 === 0 ? 'bdtop' : '', 'vcenter']" :key="item1.id" v-for="(item1, i1) in scope.row.children">
							<!-- 一级权限 -->
							<el-col :span="5">
								<el-tag @close="removeRightById(scope.row, item1.id)" closable>{{item1.authName}}</el-tag>
								<i class="el-icon-caret-right"></i>
							</el-col>
							<!-- 待循环的二级和三级权限 -->
							<el-col :span="19">
								<!-- 二级权限 -->
								<el-row :class="[i2 === 0 ? '' : 'bdtop', 'vcenter']" :key="item2.id" v-for="(item2, i2) in item1.children">
									<el-col :span="6">
										<el-tag type="success" @close="removeRightById(scope.row, item2.id)" closable>{{item2.authName}}</el-tag>
										<i class="el-icon-caret-right"></i>
									</el-col>
									<el-col :span="18">
										<el-tag @close="removeRightById(scope.row, item3.id)" closable type="warning" :key="item3.id" v-for="(item3, i3) in item2.children">{{item3.authName}}</el-tag>
									</el-col>
								</el-row>
							</el-col>
						</el-row>
					</template>
				</el-table-column>
				<el-table-column type="index"></el-table-column>
				<el-table-column label="角色名称" prop="roleName"></el-table-column>
				<el-table-column label="角色描述" prop="roleDesc"></el-table-column>
				<el-table-column label="操作" width="300px">
					<template slot-scope="scope">
						<el-button size="mini" type="primary" icon="el-icon-edit">编辑</el-button>
						<el-button size="mini" type="danger" icon="el-icon-delete">删除</el-button>
						<el-button @click="showSetRightDialog(scope.row)" size="mini" type="warning" icon="el-icon-setting">分配权限</el-button>
					</template>
				</el-table-column>
			</el-table>
		</el-card>
		<!-- 分配权限的对话框，放哪里都无所谓，只有在点击分配权限的按钮的时候才会显示出来 -->
		<el-dialog
			@close="setRightDialoogClosed"
			title="分配权限"
			:visible.sync="setRightDialogVisible"
			width="50%">
			<!-- 树形结构 -->
			<el-tree ref="treeRef" :default-checked-keys="defKeys" :default-expand-all="true" node-key="id" show-checkbox :data="rigthslist" :props="treeProps"></el-tree>
			<span slot="footer" class="dialog-footer">
				<el-button @click="setRightDialogVisible = false">取 消</el-button>
				<el-button type="primary" @click="allotRight">确 定</el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
export default {
	data() {
		return {
			// 所有角色的列表
			rolelist: [],
			// 控制分配对话框的显示与隐藏
			setRightDialogVisible: false,
			// 所有权限的数据
			rigthslist: [],
			// 树形控件的属性绑定对象
			treeProps: {
				label: 'authName',
				children: 'children'
			},
			// 默认选中的节点ID值数组
			defKeys: [],
			// 当前即将分配权限的角色id
			roleId: ''
		}
	},
	created() {
		this.getRolesList()
	},
	methods: {
		// 获取所有角色
		async getRolesList() {
			const {data: res} = await this.$http.get('roles')
			if(res.meta.status !== 200) {
				return this.$message('获取角色列表失败')
			}
			this.rolelist = res.data
			// console.log(this.rolelist);
		},
		// 根据ID删除对应的权限
		async removeRightById(role, rightId) {
			const confirmResult = await this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err => err)
			if(confirmResult !== 'confirm') {
				return this.$message.info('取消了操作')
			}
			const {data: res} = await this.$http.delete(`roles/${role.id}/rights/${rightId}`)
			if(res.meta.status !== 200) {
				return this.$message.error('删除权限失败')
			}
			// this.getRolesList()
			role.children = res.data
		},
		// 展示分配权限对话框
		async showSetRightDialog(role) {
			this.roleId = role.id
			const {data: res} = await this.$http.get('rights/tree')
			if(res.meta.status !== 200) {
				return this.$message.error('获取用户权限失败')
			}
			this.rigthslist = res.data
			// 递归获取三级节点的数据
			this.getLeafKeys(role, this.defKeys)
			this.setRightDialogVisible = true
		},
		// 通过递归的形式，获取角色下所有三级元素的ID到defKeys
		getLeafKeys(node, arr) {
			// 如果这个node节点不包含children属性，则是三级节点
			if(!node.children) {
				return arr.push(node.id)
			}
			node.children.forEach(item => {
				this.getLeafKeys(item, arr)
			});
		},
		// 分配权限对话框关闭的时候清空数组
		setRightDialoogClosed() {
			this.defKeys = []
		},
		// 点击为角色分配权限
		async allotRight() {
			const keys = [
				...this.$refs.treeRef.getCheckedKeys(),
				...this.$refs.treeRef.getHalfCheckedKeys()
			]
			const idStr = keys.join(',')
			const {data: res} = await this.$http.post(`roles/${this.roleId}/rights`, {rids: idStr})
			if(res.meta.status !== 200) {
				return this.$message.error('分配权限失败')
			}
			this.$message.success('分配权限成功')
			this.getRolesList()
			this.setRightDialogVisible = false
		}

	}
}
</script>


<style lang="less" scoped>
.el-tag {
	margin: 7px;
}
.bdtop {
	border-top: 1px solid #eee;
}
.bdbottom {
	border-bottom: 1px solid #eee;
}
.vcenter {
	display: flex;
	align-items: center;
}
</style>