<template>
  <div>
    <!-- 面包屑导航区 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片区 -->
    <el-card>
      <!-- 搜索与添加 -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input @clear="getUserList" clearable placeholder="请输入内容" v-model="queryInfo.query">
            <el-button @click="getUserList" slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>

        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true">添加用户</el-button>
        </el-col>
      </el-row>

      <!-- y用户列表区 -->
      <el-table :data="userlist" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="姓名" prop="username"></el-table-column>
        <el-table-column label="邮箱" prop="email"></el-table-column>
        <el-table-column label="电话" prop="mobile"></el-table-column>
        <el-table-column label="角色" prop="role_name"></el-table-column>
        <el-table-column label="状态">
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.mg_state" @change="userWtateChanged(scope.row)">
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="180px">
					<template slot-scope="scope">
						<!-- 修改按钮 -->
						<el-button @click="showEditDialog(scope.row.id)" type="primary" icon="el-icon-edit" size="mini"></el-button>
						<!-- 删除按钮 -->
						<el-button @click="removeUserById(scope.row.id)" type="danger" icon="el-icon-delete" size="mini"></el-button>
						<!-- 分配角色按钮 -->
						<el-tooltip effect="dark" content="分配角色" placement="top" :enterable="false">
							<el-button @click="setRole(scope.row)" type="warning" icon="el-icon-setting" size="mini"></el-button>
						</el-tooltip>
						
					</template>
				</el-table-column>
      </el-table>

			<!-- 分页区 -->
			<el-pagination
    	@size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryInfo.pagenum"
      :page-sizes="[1, 2, 5, 10]"
      :page-size="queryInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    	</el-pagination>
    </el-card>
		<!-- 添加用户的对话框，点击按钮后才显示，所以放哪里无所谓 -->
		<el-dialog
			title="添加用户"
			:visible.sync="addDialogVisible"
			width="50%"
			@close="addDialogClosed">
			<!-- 内容主体区域 -->
			<el-form ref="addFormRef" :model="addForm" :rules="addFormRules" label-width="70px">
				<el-form-item label="用户名" prop="username">
					<el-input v-model="addForm.username"></el-input>
				</el-form-item>
				<el-form-item label="密码" prop="password">
					<el-input v-model="addForm.password"></el-input>
				</el-form-item>
				<el-form-item label="邮箱" prop="email">
					<el-input v-model="addForm.email"></el-input>
				</el-form-item>
				<el-form-item label="手机" prop="mobile">
					<el-input v-model="addForm.mobile"></el-input>
				</el-form-item>
			</el-form>
			<!-- 底部 -->
			<span slot="footer" class="dialog-footer">
				<el-button @click="addDialogVisible = false">取 消</el-button>
				<el-button type="primary" @click="addUser">确 定</el-button>
			</span>
		</el-dialog>
		<!-- 修改用户信息的对话框 -->
		<el-dialog
			title="修改用户"
			:visible.sync="editDialogVisible"
			@close="editDialogClose"
			width="50%">
			<el-form ref="editFormRef" :model="editForm" :rules="editFormRules" label-width="70px">
				<el-form-item label="用户名">
					<el-input v-model="editForm.username" disabled></el-input>
				</el-form-item>
				<el-form-item label="邮箱" prop="email">
					<el-input v-model="editForm.email"></el-input>
				</el-form-item>
				<el-form-item label="手机" prop="mobile">
					<el-input v-model="editForm.mobile"></el-input>
				</el-form-item>
			</el-form>
			<span slot="footer" class="dialog-footer">
				<el-button @click="editDialogVisible = false">取 消</el-button>
				<el-button type="primary" @click="editUserInfo">确 定</el-button>
			</span>
		</el-dialog>

		<!-- 分配角色的对话框 -->
		<el-dialog
			title="分配角色"
			:visible.sync="setRoleDialogVisible"
			width="50%"
			@close="setRoleDialogClosed">
			<div>
				<p>当前的用户：{{userInfo.username}}</p>
				<p>当前的角色：{{userInfo.role_name}}</p>
				<p>分配新角色：
					<el-select v-model="selectedRoleId" placeholder="请选择">
						<el-option
							v-for="item in getRolesList"
							:key="item.id"
							:label="item.roleName"
							:value="item.id">
						</el-option>
					</el-select>
				</p>
			</div>
			<span slot="footer" class="dialog-footer">
				<el-button @click="setRoleDialogVisible = false">取 消</el-button>
				<el-button type="primary" @click="saveRoleInfo">确 定</el-button>
			</span>
		</el-dialog>
  </div>
</template>

<script>
export default {
  data() {
		var checkEmail = (rule, value, cb)=>{
			// 验证邮箱的规则
			const regEmail = /^((([a-z0-9_\.-]+)@([\da-z\.-]+)\.([a-z\.]{2,6}\;))*(([a-z0-9_\.-]+)@([\da-z\.-]+)\.([a-z\.]{2,6})))$/
			if(regEmail.test(value)) {
				return cb()
			}
			cb(new Error('请输入合法的邮箱'))
		}
		var checkMobile = (rule, value, cb)=>{
			// 验证手机号的规则
			const regMobile = /^((\+?86)|(\(\+86\)))?(13[012356789][0-9]{8}|15[012356789][0-9]{8}|18[02356789][0-9]{8}|147[0-9]{8}|1349[0-9]{7})$/
			if(regMobile.test(value)) {
				return cb()
			}
			cb(new Error('请输入合法的手机号'))
		}
    return {
      // 获取用户列表的参数对象
      queryInfo: {
        query: '',
				// 当前页数
        pagenum: 1,
				// 当前每页显示多少条数据
        pagesize: 2,
      },
      userlist: [],
      total: 0,
			// 控制添加对话框的显示与隐藏
			addDialogVisible: false,
			// 添加用户的表单数据
			addForm: {
				username: '',
				password: '',
				email: '',
				mobile: ''
			},
			// 查询到的用户信息对象
			editForm: {},
			// 控制修改用户对话框的显示
			editDialogVisible: false,
			// 控制分配你权限对话框的显示
			setRoleDialogVisible: false,
			// 需要被分配权限的用户信息
			userInfo: {},
			// 所有角色的数据列表
			getRolesList: [],
			// 分配新角色选中的值
			selectedRoleId: '',
			// 添加表单的验证规则对象
			addFormRules: {
				username: [
					{ required: true, message: '请输入用户名', trigger: 'blur' },
					{ min: 3, max: 10, message: '用户名的长度在3~10个字符之间', trigger: 'blur'  }
				],
				password: [
					{ required: true, message: '请输入密码', trigger: 'blur' },
					{ min: 6, max: 15, message: '用户名的长度在6~15个字符之间', trigger: 'blur'  }
				],
				email: [
					{ required: true, message: '请输入邮箱', trigger: 'blur' },
					{ validator: checkEmail, trigger: 'blur' }
				],
				mobile: [
					{ required: true, message: '请输入手机号', trigger: 'blur' },
					{ validator: checkMobile, trigger: 'blur' }
				]
			},
			// 修改用户信息的校验规则
			editFormRules: {
				email: [
					{ required: true, message: '请输入邮箱', trigger: 'blur' },
					{ validator: checkEmail, trigger: 'blur' }
				],
				mobile: [
					{ required: true, message: '请输入手机号', trigger: 'blur' },
					{ validator: checkMobile, trigger: 'blur' }
				]
			}
    }
  },
  created() {
    this.getUserList()
  },
  methods: {
    async getUserList() {
      const { data: res } = await this.$http.get('users', {
        params: this.queryInfo,
      })
      console.log(res)
      if (res.meta.status !== 200)
        return this.$message.error('获取用户列表失败')
      this.userlist = res.data.users
      this.total = res.data.total
    },
		// 监听pagesize改变的事件
		handleSizeChange(newSize) {
			this.queryInfo.pagesize = newSize
			this.getUserList()
			// console.log(newSize);
		},
		// 监听页码值改变
		handleCurrentChange(newPage) {
			// console.log(newPage);
			this.queryInfo.pagenum = newPage
			this.getUserList()
		},
		async userWtateChanged(userinfo) {
			// console.log(userinfo);
			const {data: res} = await this.$http.put(`users/${userinfo.id}/state/${userinfo.mg_state}`)
			if(res.meta.status !== 200) {
				userinfo.mg_state = !userinfo.mg_state
				return this.message.error('更新用户状态失败')
			}
			this.$message.success('更新状态成功')
		},
		// 对话框关闭，清空表单
		addDialogClosed() {
			this.$refs.addFormRef.resetFields()
		},
		// 添加用户确定事件
		addUser() {
			this.$refs.addFormRef.validate(async valid =>{
				if(!valid) return 
				const {data: res} = await this.$http.post('users', this.addForm)
				if(res.meta.status !== 201) {
					this.$message.error('添加用户失败')
				}
				this.$message.success('添加成功')
				this.addDialogVisible = false
				this.getUserList()
			})
		},
		// 编辑用户对话框
		async showEditDialog(id) {
			const {data: res} = await this.$http.get('users/' + id)
			if(res.meta.status !== 200) {
				return this.$message.error('查询用户信息失败')
			}
			this.editForm = res.data
			// 将对话框显示出来
			this.editDialogVisible = true
		},
		// 修改用户对话框关闭后重置
		editDialogClose() {
			this.$refs.editFormRef.resetFields()
		},
		// 修改用户信息
		editUserInfo() {
			this.$refs.editFormRef.validate(async valid=>{
				if(!valid) return
				const {data: res} = await this.$http.put('users/' + this.editForm.id, {
					email: this.editForm.email,
					mobile: this.editForm.mobile})
				if(res.meta.status !== 200) {
					return this.$message.error('更新用户信息失败')
				}
				// 修改成功关闭对话框
				this.editDialogVisible = false
				// 刷新数据列表
				this.getUserList()
				this.$message.success('更新用户信息成功')
			})
		},
		// 删除用户
		async removeUserById(id) {
			const confirmResult = await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err => err)
			// 如果用户点击了确认删除，confirmResult为confirm文本
			// 如果用户点击了取消删除，confirmResult为cancel文本
			if(confirmResult !== 'confirm') {
				return this.$message.info('已取消删除')
			}
			const {data: res} = await this.$http.delete('users/' + id)
			if(res.meta.status !== 200) {
				return this.$message.error('删除用户失败')
			}
			this.$message.success('删除用户成功')
			// 刷新用户列表
			this.getUserList()
		},
		// 显示分配角色对话框
		async setRole(userInfo) {
			this.userInfo = userInfo
			// 展示对话框之前获取所有角色的列表
			const {data: res} = await this.$http.get('roles')
			if(res.meta.status !== 200) {
				return this.$message.error('获取角色列表失败')
			}
			this.getRolesList = res.data
			this.setRoleDialogVisible = true
		},
		// 点击按钮分配角色
		async saveRoleInfo() {
			if(!this.selectedRoleId) {
				return this.$message.error('请选择要分配的角色')
			}
			const {data: res} = await this.$http.put(`users/${this.userInfo.id}/role`, {rid: this.selectedRoleId})
			if(res.meta.status !== 200) {
				return this.$message.error('更新角色失败')
			}
			this.$message.success('更新角色成功')
			this.getUserList()
			this.setRoleDialogVisible = false
		},
		// 分配角色对话框关闭后，将选中的清空
		setRoleDialogClosed() {
			this.selectedRoleId = ''
			this.userInfo = {}
		}
  },
}
</script>

<style lang="less" scoped>
</style>